	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import forum from '@/views/forum/list'
	import baoxiuxinxi from '@/views/baoxiuxinxi/list'
	import weixiubaoxiu from '@/views/weixiubaoxiu/list'
	import weixiuyuan from '@/views/weixiuyuan/list'
	import jieguofankui from '@/views/jieguofankui/list'
	import baoxiufenpei from '@/views/baoxiufenpei/list'
	import xuesheng from '@/views/xuesheng/list'
	import sushexinxi from '@/views/sushexinxi/list'
	import baoxiuchuli from '@/views/baoxiuchuli/list'
	import config from '@/views/config/list'
	import users from '@/views/users/list'
	import weixiuyuanCenter from '@/views/weixiuyuan/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/weixiuyuanCenter',
			name: '维修员个人中心',
			component: weixiuyuanCenter
		}
		,{
			path: '/news',
			name: '公告信息',
			component: news
		}
		,{
			path: '/forum',
			name: '我的发布',
			component: forum
		}
		,{
			path: '/baoxiuxinxi',
			name: '报修信息',
			component: baoxiuxinxi
		}
		,{
			path: '/weixiubaoxiu',
			name: '维修报修',
			component: weixiubaoxiu
		}
		,{
			path: '/weixiuyuan',
			name: '维修员',
			component: weixiuyuan
		}
		,{
			path: '/jieguofankui',
			name: '结果反馈',
			component: jieguofankui
		}
		,{
			path: '/baoxiufenpei',
			name: '报修分配',
			component: baoxiufenpei
		}
		,{
			path: '/xuesheng',
			name: '学生',
			component: xuesheng
		}
		,{
			path: '/sushexinxi',
			name: '宿舍信息',
			component: sushexinxi
		}
		,{
			path: '/baoxiuchuli',
			name: '报修处理',
			component: baoxiuchuli
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
